@import "../../style.scss";

.skills {
    font-size: 2vmin;
    margin-left: 5vmin;
    margin-right: 4vmin;
    padding: 2vmin;
    border-radius: 2vmin;
    cursor: pointer;
    transition: all 0.5s ease;

    &.dynamic {
        background-color: $mainColor;
        color: aliceblue;
    }
}